import React from 'react';
import { Switch, Redirect } from 'react-router-dom';

import { RouteWithLayout } from './components';
import { Main as MainLayout, Minimal as MinimalLayout } from './layouts';

import {
  Size as VideoView,
  Photo as PhotoView,
  Roman as RomanView,
  Story as StoryView,

  Dashboard as DashboardView,
  ProductList as ProductListView,
  UserList as UserListView,
  Typography as TypographyView,  
  Account as AccountView,
  Settings as SettingsView,
  SignUp as SignUpView,
  SignIn as SignInView,
  NotFound as NotFoundView
} from './views';

const Routes = () => {

  return (
    <Switch>
      <Redirect
        exact
        from="/"
        to="/photo"
      />      
      <RouteWithLayout
        component={PhotoView}
        exact
        layout={MainLayout}        
        name="photo"
        path="/photo"
      />

      <RouteWithLayout
        component={RomanView}
        exact
        layout={MainLayout}
        name="phylactere"
        path="/phylactere"
      />

      <RouteWithLayout
        component={StoryView}
        exact
        layout={MainLayout}
        name="story"
        path="/story"
      />
      <RouteWithLayout
        component={VideoView}
        exact
        layout={MainLayout}        
        name="Video"
        path="/video"
      />
      
      <Redirect to="/not-found" />
    </Switch>
  );
};

export default Routes;
